import React from 'react';
import { Button, Form, FormGroup, Label, Input, Row, Col } from 'reactstrap';

class TypeItIn extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      name: '',
      initialsName: ''
    };
  }
  render() {
    return (
      <>
        <Row>
          <Col sm="12">
            <h2 className="text-center">Create your Signature</h2>
          </Col>
        </Row>
        <Form>
          <Row>
            <Col>
              <FormGroup>
                <Label for="confirm-name">Confirm your name</Label>
                <Input
                  type="text"
                  name="name"
                  id="confirm-name"
                  placeholder="Confirm your name"
                  onChange={event =>
                    this.setState({ name: event.target.value })
                  }
                />
              </FormGroup>
            </Col>
            <Col sm="4">
              <FormGroup>
                <Label for="confirm-initials">Confirm your initials</Label>
                <Input
                  type="text"
                  name="initials"
                  id="confirm-initials"
                  placeholder="Confirm your initials"
                  onChange={event =>
                    this.setState({ initialsName: event.target.value })
                  }
                />
              </FormGroup>
            </Col>
          </Row>
          <Row>
            <Col>
              <Label for="confirm-name">Preview</Label>
            </Col>
          </Row>
          <Row>
            <Col>
              <FormGroup>
                <span
                  style={{
                    border: '12px solid #CCCCCC',
                    background: '#ffffff',
                    padding: '8px 15px',
                    display: 'block',
                    fontFamily: 'Times New Roman '
                  }}
                  className="name-preview"
                >
                  {this.state.name}
                </span>
              </FormGroup>
            </Col>
            <Col sm="4">
              <FormGroup>
                <span
                  style={{
                    border: '12px solid #CCCCCC',
                    background: '#ffffff',
                    padding: '8px 15px',
                    display: 'inline-block'
                  }}
                  className="initials-preview"
                >
                  {this.state.initialsName}
                </span>
              </FormGroup>
            </Col>
          </Row>

          <Row>
            <Col>
              <p>
                By clicking Accept and Sign, I agree that the signature wil be
                the electric representation of my signature for all purposewhen
                I for my agend use themon documents. Including legallybinding
                documnets, just the same as pen and paper signature.{' '}
              </p>
            </Col>
          </Row>

          <Row>
            <Col>
              <FormGroup style={{ display: 'flex' }}>
                <Label for="change-font" style={{ flex: 3 }}>
                  Change Font:
                </Label>
                <select
                  className="form-control"
                  style={{ flex: 9 }}
                  id="change-font"
                >
                  <option>Man Salva</option>
                  <option>Times New Roman</option>
                  <option>Google Font #12</option>
                </select>
              </FormGroup>
            </Col>
            <Col sm="4">
              <FormGroup>
                <Button color="primary">Accept and Sign</Button>
              </FormGroup>
            </Col>
          </Row>
        </Form>
      </>
    );
  }
}

export default TypeItIn;
